.education-body{
    width: 90%;
    height: 90%;
    padding: 3%;
    display: flex;
    flex-wrap: wrap;
    overflow-y: auto ;
    justify-content: space-between;
    align-items: center;
}

.edu-div{
    background-color: #333;
    border-radius: 10px;
}

.education-card{
    width: 30%;
    height: 50%;
    border-radius: 20px;
    border: 3px solid black;
    align-items: center;
    justify-content: space-between;
    background-color: black;
}

.edu-img{
    width: 25%;
    aspect-ratio: 1;
    border-radius: 5px;
}

p{
    font-size: 15px;
    color: white;
}

.school-name{
    font-weight: 500;
}

.desc{
    font-weight: 500;
}

.level{
    font-weight: 300;
}

h2{
    padding-top: 50px;
    font-size: 36px;
    color: white;
}
  