.header {
    padding: 10px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #333;
}
  
.header ul {
    list-style: none;
    display: flex;
    justify-content: flex-end;
    margin: 0;
    padding: 0;
}

.header li {
    margin-left: 20px;
    font-family: "Montserrat";
    color: #fff;
}

.montserrat-li {
    font-family: "Montserrat", sans-serif;
    font-optical-sizing: auto;
    font-weight: 400;
    font-style: normal;
  }
.header a {
    text-decoration: none;
    color: #fff;
}
  
.initials{
    font-family: "Playwrite NO";
    color: #fff;
}

.playwrite-es-deco-initials {
    font-family: "Playwrite ES Deco", cursive;
    font-optical-sizing: auto;
    font-weight: 200;
    font-style: normal;
  }

#sg{
    font-family: "Playwrite NO";
    color: #fff;
}