.profile {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 50px 20px;
    max-width: 1200px;
    margin: 0 auto;
    background: #333;
    justify-content: left;
    border-radius: 0px;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
  }
  
  .profile-content {
    flex: 1;
    padding-right: 20px;
    text-align: left;
    display: inline block;
    width: 60%;
    justify-content: space-between;
    color: #fff;
  }
  
  .profile-image-container {
    flex: 0 0 250px;
  }
  
  .profile-image {
    width: 100%;
    height: auto;
    border-radius: 10px;
  }

  
  p {
    font-size: 16px;
    line-height: 1.5;
    margin: 10px 0;
  }
  
  .profile-buttons {
    margin-top: 20px;
  }
  
  .button {
    text-decoration: none;
    font-family: "Montserrat";
    padding: 10px 20px;
    margin: 0 0px;
    margin-right: 30px;
    border-radius: 5px;
    border: 2px solid black;
  }

  #btn1{
    background-color: #000;
    color: white;
  }

  #btn2{
    background-color: #000;
    color: white;
  }
  
#title{
  font-size: 60px;
  margin: 0%;
  margin-top: 10px;
  font-family: "Montserrat";
}

#mon{
  font-family: "Montserrat";
}